body {
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  background-color: #fff;
  border-radius: 8px;
  max-width: 600px;
  margin: 50px auto;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

h1 {
  text-align: center;
}

.form-group {
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

#submitBtn:hover {
  background-color: #45a049;
}

#submitBtn {
  color: #fff;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
}

.form-group label {
  margin-bottom: 5px;
  font-weight: bold;
}

#email, #message, #name {
  box-sizing: border-box;
  background-color: #d6e2e6;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  margin-top: 5px;
  padding: 10px;
  font-size: 16px;
}
